<template>
    <div class="port-box">
        <div class="left-box">
            <div class="use-port-line">
                <span class="port-text">{{ name }}</span>
                <!-- <span class="port-btn">
                    <LeetxSwitch :module="module" :height="2" :value="pData.enable" :activeVal="true" :staticVal="false" v-model="pData.enable"></LeetxSwitch>
                </span> -->
            </div>
            <svg-loader class="icon" icon="Port"></svg-loader>
        </div>
        <div class="right-box">
            <div class="input-line">
                <span class="label-text">{{ $t('setserialport.baudrate') }}</span>
                <span class="label-data">
                    <NewSelect :module="module" v-model:realVal="pData.baud_rate" :options="bData"></NewSelect>
                </span>
            </div>
            <div class="input-line">
                <span class="label-text">{{ $t('setserialport.endMark') }}</span>
                <span class="label-data">
                    <NewSelect :module="module" v-model:realVal="pData.end_symbl" :options="eData"></NewSelect>
                </span>
            </div>
            <!-- <div class="input-line">
                <span class="label-text">{{$t('setserialport.databits')}}</span>
                <span class="label-data">
                    <NewSelect :module="module" @valChng="modData($event,'data_bits')" :realVal="pData.data_bits" :options="nData"></NewSelect>
                </span>
            </div>
            <div class="input-line">
                <span class="label-text">{{$t('setserialport.stopbits')}}</span>
                <span class="label-data">
                    <NewSelect :module="module" @valChng="modData($event,'stop_bits')" :realVal="pData.stop_bits"  :options="sData"></NewSelect>
                </span>
            </div>
            <div class="input-line">
                <span class="label-text">{{$t('setserialport.stopbits')}}</span>
                <span class="label-data">
                    <NewSelect :module="module" @valChng="modData($event,'parity')" :realVal="pData.parity" :options="checkData"></NewSelect>
                </span>
            </div> -->
            <!-- <div class="input-line">
                <span class="label-text">{{$t('setserialport.mode')}}</span>
                <span class="label-data">
                    <NewSelect :module="module" @valChng="modData($event,'mode')" :realVal="pData.mode" :options="modes"></NewSelect>
                </span>
            </div> -->
        </div>
    </div>
</template>
<script>
// import LeetxSwitch from '../switch/LeetxSwitch.vue'
import NewSelect from '@/components/select/NewSelect.vue'

export default {
    components: {
        // LeetxSwitch,
        NewSelect
    },
    data() {
        return {
            module: "DEV_BUS",
            pData: {},
            pName: undefined,
            bData: { 9600: 9600, 14400: 14400, 19200: 19200, 38400: 38400, 115200: 115200 },
            eData: { [this.$t('setserialport.cr')]: 13, [this.$t('setserialport.lf')]: 10, [this.$t('setserialport.space')]: 32, [this.$t('setserialport.tab')]: 9, [this.$t('setserialport.nul')]: 0 },
            nData: { 5: 5, 6: 6, 7: 7, 8: 8 },
            sData: { 1: 1, 2: 2 },
            checkData: { "NONE": 0, "ODD": 1, "EVEN": 2 },
            modes: { "Scaner": 0, "WSI": 1, "OP": 2 },
        }
    },
    props: {
        cfgData: {
            type: Object,
            default: function () {
                return {
                    "serial_port": "RS232-A",
                    "baud_rate": 14400,
                    "end_symbl": 13,
                    "data_bits": 8,
                    "stop_bits": 1,
                    "parity": 0,
                    "mode": 0,
                    "enable": false
                }
            }
        },
        name: {
            type: String,
            default: ""
        },

    },
    created() {
        this.initPdata()
    },
    methods: {
        initPdata() {
            // this.pName=Object.keys(this.portData)[0]
            // this.pData=deepCopy(this.portData[this.pName])
            this.pData = this.cfgData
        },
    },

    watch: {
        pData: {
            deep: true,
            handler: function () {
                this.$emit("update:cfgData", this.pData)
            }
        },
        cfgData: {
            deep: true,
            handler: function () {
                this.initPdata()
            }
        }
    }
}
</script>
<style scoped>
.port-box {
    position: relative;
    height: 27.4375vh;
}

.left-box {
    position: absolute;
    left: 5.125vw;
    height: 27.4375vh;
    width: 30vw;
}

.port-text {
    position: absolute;
    font-size: var(--font1_625) !important;
    color: var(--C00);
    bottom: 0;

}

.right-box {
    position: absolute;
    right: 5.175vw;
    height: 27.4375vh;
    width: 50.5vw;
    flex-direction: column;
}

.use-port-line {
    position: relative;
    height: 3.9063vh;
    width: 100%;

    font-size: var(--font1_625) !important;
    color: var(--C00);
}

.use-port-line span:nth-child(1) {
    float: left;
    position: relative;
    left: 0;
    font-size: var(--font1_5);

}

.use-port-line span:nth-child(2) {
    position: absolute;
    left: 9rem;

}

.icon {
    position: absolute;
    left: 0;
    top: 25%;
    width: 30vw;
    height: 16.6094vh;
}

.input-line {
    position: relative;
    height: 3.9063vh;
    width: 100%;
    margin-bottom: 1.9765vh;
}

.label-text {
    position: absolute;
    font-size: var(--font1_625);
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    color: var(--C3B);
}

.label-data {
    position: absolute;
    right: 0;
    bottom: 0rem;

}

.submit-data {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;

}
</style>