<template>
    <div class="srlprt-box">
        <SerialPortConf :port="1" v-model:cfgData="serial_port_wsi" name="WSI" :key="1"></SerialPortConf>
        <div class="horizontal-line"></div>
        <SerialPortConf :port="2" v-model:cfgData="serial_port_scaner" name="SCANER" :key="2">
        </SerialPortConf>
        <LeetxButton :module="module" :enablerun="false" class="confirm-box" textColor="#fff" bgcColor="#00c155"
            width="60.625vw" height="3.75rem" fontSize="2.25rem" fontFamily="--SHSC-B" :text="$t('confirm.confirm')"
            @click="submitPortData"></LeetxButton>
    </div>
</template>
<script>
import cnfrmWin from '@/dialogs/common/confirm/cnfrm'
import LeetxButton from '@/components/controls/LeetxButton.vue'
import SerialPortConf from "./SerialPortConf.vue"
import { devCfgSerialRs232, devCfgSerialSet } from "@/api/device/device"
import { messageApi } from '@/components/message/message.js'
export default {
    components: {
        LeetxButton,
        SerialPortConf
    },
    data() {
        return {
            module: "DEV_BUS",
            serial_port_wsi: {
                "serial_port": "RS232-A",
                "baud_rate": 9600,
                "end_symbl": 13,
                "data_bits": 8,
                "stop_bits": 1,
                "parity": 1,
                "mode": 2,
                "enable": true
            },
            serial_port_scaner: {
                "serial_port": "RS232-B",
                "baud_rate": 9600,
                "end_symbl": 13,
                "data_bits": 8,
                "stop_bits": 1,
                "parity": 1,
                "mode": 2,
                "enable": true
            },
            global_wsi_data: {},
            global_scaner_data: {},
            // 退出界面检查标志
            need_check_edit: false,
        }
    },
    methods: {
        submitPortData() {
            let WSI = this.serial_port_wsi
            let SCANER = this.serial_port_scaner
            let params = { WSI, SCANER }
            devCfgSerialSet(params).then((result) => {
                if (result.status == 0) {
                    // let msg = this.$t('settable.success')
                    // messageApi.success([msg])
                    messageApi.success(['保存成功'])
                }
                if (result.status == 9081) {
                    messageApi.error(["下发串口配置失败"])
                    return
                }
            })
            this.need_check_edit = false
        },
        getData(key) {
            let obj = {}
            obj[key] = this.portData[key]
            return obj
        },
        // 从后台拿数据
        getPortData() {
            devCfgSerialRs232().then((result) => {
                if (result.status == 0) {
                    this.need_check_edit = true
                    this.serial_port_wsi = result.data.WSI
                    this.serial_port_scaner = result.data.SCANER
                    this.global_wsi_data = JSON.stringify(this.serial_port_wsi)
                    this.global_scaner_data = JSON.stringify(this.serial_port_scaner)
                }
            }).catch(() => {
                messageApi.error(['获取串口失败!'])
            }
            )

        },
        clbck(name, data) {
            this.portData[name] = data
        },

    },
    beforeRouteLeave(to, from, next) {
        let global_wsi_unchange = this.global_wsi_data == JSON.stringify(this.serial_port_wsi)
        let global_scaner_unchange = this.global_scaner_data == JSON.stringify(this.serial_port_scaner)
        if (global_wsi_unchange && global_scaner_unchange || this.$store.state.g_status.busy || this.need_check_edit == false) {
            next()
        } else {
            let title = this.$t('setcontrol.saveSet')
            let confirmText = this.$t('setcontrol.yes')
            let cancelText = this.$t('setcontrol.no')
            let msg = this.$t('setcontrol.isSaveSet')
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(() => {
                this.submitPortData()
                next()
            }).catch(() => {
                next()
            });
        }

    },
    watch: {
    },
    created() {
        this.getPortData()
    }
}
</script>
<style scoped>
.srlprt-box {
    position: relative;
    width: 100%;
    height: calc(100% - 1rem);
    display: flex;
    flex-direction: column;
    top: 2.5rem;
}

.confirm-box {
    position: absolute;
    bottom: 5rem;
    left: 50%;
    transform: translateX(-50%);
}

.horizontal-line {
    position: relative;
    margin-bottom: calc(1.2rem + 2px);
    margin-top: 1.2rem;
    height: 1px;
    width: calc(100% - 50px);
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--Cd4);
}
</style>